@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600&family=Poppins:wght@300;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
  background-color: white;
}

.pageWrap {
  background-color: #eaf2fa;
  position: relative;
  overflow: hidden;
  min-height: calc(100vh - 40px);
  max-width: 96%;
  margin: 2%;
}
.title {
  color: rgb(32, 32, 108);
  font-size: xxx-large;
  -webkit-animation: reveal 1s cubic-bezier(.215,.61,.355,1) .25s backwards;
          animation: reveal 1s cubic-bezier(.215,.61,.355,1) .25s backwards;
  text-align: right;
}
@-webkit-keyframes reveal {
    from {opacity:0%; -webkit-transform: translateY(8px); transform: translateY(8px);}
  to {opacity: 100%;}
}
@keyframes reveal {
    from {opacity:0%; -webkit-transform: translateY(8px); transform: translateY(8px);}
  to {opacity: 100%;}
}
.subText{
    color:rgb(24, 24, 83);
    -webkit-animation: reveal 1s cubic-bezier(.215,.61,.355,1) .25s backwards;
            animation: reveal 1s cubic-bezier(.215,.61,.355,1) .25s backwards;
    text-align: right;
}

