.pageWrap {
  background-color: #eaf2fa;
  position: relative;
  overflow: hidden;
  min-height: calc(100vh - 40px);
  max-width: 96%;
  margin: 2%;
}
.title {
  color: rgb(32, 32, 108);
  font-size: xxx-large;
  animation: reveal 1s cubic-bezier(.215,.61,.355,1) .25s backwards;
  text-align: right;
}
@keyframes reveal {
    from {opacity:0%; transform: translateY(8px);}
  to {opacity: 100%;}
}
.subText{
    color:rgb(24, 24, 83);
    animation: reveal 1s cubic-bezier(.215,.61,.355,1) .25s backwards;
    text-align: right;
}

